:root {
  --bg-color: #09c;
  --text-color: rgba(255, 255, 255, 0.9);
  --link-color: #fff;
  --code-bg-color: rgb(0, 77, 102);
  --code-text-color: hsl(195, 100%, 50%);
}
* {
  box-sizing: border-box;
  /* margin: 0; */
  padding: 0;
}
body {
  font-size: 12px;
  line-height: 20px;
  margin: 0;

  background-color: #09c;

  background-color: var(--bg-color);
  color: rgba(255, 255, 255, 0.9);
  color: var(--text-color);
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
a {
  color: #fff;
  color: var(--link-color);
}
h1 {
  font-size: 3em;
  line-height: 1.15em;
  font-weight: 200;
  margin: 30px 0 20px;
}
h2 {
  margin: 0 0 30px;
}
section {
  margin-bottom: 35px;
  padding-bottom: 35px;
  border-bottom: 3px solid;
  border-bottom-color: rgb(0, 138, 184);
  border-bottom-color: rgb(0, 138, 184);
}
section:after {
    content: "."; display: block; clear: both; visibility: hidden; line-height: 0; height: 0;
  }
section:last-child {
    border-bottom-width: 0;
  }
.wrapper {
  max-width: 480px;
  width: 100%;
  margin: 0 auto;
}
.center {
  text-align: center;
}
.button {
  display: inline-block;
  margin: 0 15px 0 0;
  font-size: 1.1em;
  padding: 5px 15px;
  text-decoration: none;
  border-radius: 3px;
  background-color: #fff;
  background-color: var(--link-color);
  color: #09c;
  color: var(--bg-color);
  transition: opacity 150ms ease;
}
.button:hover {
    opacity: .9;
  }
.button span {
    display: block;
    font-weight: bold;
    font-size: 1.25em;
    margin-bottom: 5px;
  }
.button.secondary {
    box-shadow: inset 0 0 0 2px rgb(0, 107, 143);
    box-shadow: inset 0 0 0 2px rgb(0, 107, 143);
    background-color: transparent;
    color: #fff;
    color: var(--link-color);
  }
pre {
  font-size: 13px;
  line-height: 20px;
  padding: 15px 20px;
  margin: 20px 0 0;
  overflow: auto;

  background: rgb(0, 77, 102);

  background: var(--code-bg-color);
  color: hsl(195, 100%, 50%);
  color: var(--code-text-color);
  border-radius: 3px;
}
footer {
  margin: 100px 0 30px;
}
@media all and (max-width: 500px) {
  body {padding: 0 20px}
}
.psd-guides-wrapper {
  pointer-events: none;
}
.psdguides-ctrl--fixed {
  position: fixed;
  left: 10px;
  top: 10px;
  padding: 5px 10px;
  border: 1px solid rgb(0, 0, 0);
  border-radius: 3px;
  color: rgb(240, 240, 240);
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 11px;
  font-weight: bold;
  z-index: 10000;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.701961);
}
/* github corner */
.github-corner {
  position: fixed;
  top: 0;
  right: 0;
}
.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out
}
.github-corner > svg {
  display: block;
  border: 0;
  fill: rgba(0,0,0,0.70196);
  color: #09c;
  color: var(--bg-color);
}
@keyframes octocat-wave {
  0%,100%{transform:rotate(0)}
  20%,60%{transform:rotate(-25deg)}
  40%,80%{transform:rotate(10deg)}
}
@media (max-width:500px){
  .github-corner:hover .octo-arm{animation:none}
  .github-corner .octo-arm{animation:octocat-wave 560ms ease-in-out}
}